	module.exports = {"data":{"markdownRemark":{"html":"<p>Programs can import your reports from external issue trackers into HackerOne. When reports are imported, you’ll be invited to claim your report so that you can continue to access and work on them as well as earn reputation for reputable reports.</p>\n<p>You’ll receive Invitations to claim your reports through email. To claim your report:  </p>\n<ol>\n<li>Open the email you received to claim your report.</li>\n<li>Click <b>Claim your report</b>.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-c4169.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.7477696674777%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"claim your report email\"\n        title=\"\"\n        src=\"/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-2f183.png\"\n        srcset=\"/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-67829.png 125w,\n/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-5c59f.png 250w,\n/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-2f183.png 500w,\n/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-c93e8.png 750w,\n/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-4e628.png 1000w,\n/static/claiming-report-1-5638161ac26791673c5a50daf6ab10bc-c4169.png 1233w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"3\">\n<li>Select to either <strong>Accept</strong> or <strong>Reject</strong> the invitation to claim the report.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/claiming-report-2-7329dafa5c18ac47c41fc3b2c9300eda-cfda7.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 101.22075279755849%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Claiming the report\"\n        title=\"\"\n        src=\"/static/claiming-report-2-7329dafa5c18ac47c41fc3b2c9300eda-2f183.png\"\n        srcset=\"/static/claiming-report-2-7329dafa5c18ac47c41fc3b2c9300eda-67829.png 125w,\n/static/claiming-report-2-7329dafa5c18ac47c41fc3b2c9300eda-5c59f.png 250w,\n/static/claiming-report-2-7329dafa5c18ac47c41fc3b2c9300eda-2f183.png 500w,\n/static/claiming-report-2-7329dafa5c18ac47c41fc3b2c9300eda-c93e8.png 750w,\n/static/claiming-report-2-7329dafa5c18ac47c41fc3b2c9300eda-cfda7.png 983w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>If you accept to claim your report, you’ll have access to your report in HackerOne where you can take <a href=\"report-actions.html\">actions</a> on the report. Depending on the <a href=\"report-states.html\">report state</a>, you’ll also properly be awarded with <a href=\"reputation.html\">reputation</a>.  If you select to reject the invitation, you won't be able to participate</p>","frontmatter":{"title":"Claiming Reports","id":"hackers/claiming-reports","path":"/hackers/claiming-reports.html","description":null}}},"pathContext":{}}

